<template>
  <div style="height: 200px; overflow-y: auto; padding-right: 20px;">
    <div v-for="(item, index) in txList" :key="index">
      <div style="display: flex; flex-direction: row; justify-content: space-between; align-items: center;">
        <a :href="getExplorerUrl('tx', item.hash)" target="_blank" class="link-a">{{formatTxInfo(item)}}</a>
        <img v-if="txPending(item)" src="../../assets/blue-loader.249140da.svg" alt="loader" class="tx-list-status-img loading-img">
        <svg v-else class="icon" style="font-size: 20px;" aria-hidden="true">
          <use xlink:href="#icon-color-success"></use>
        </svg>
      </div>
    </div>
  </div>
</template>
<script>
import common from "@/utils/common"
import config from "@/utils/config"
import base from "@/utils/base"

export default {
  mixins: [base],
  name: 'TxList',
  props: {
  },
  components: {
  },
  computed: {
    txList() {
      const list = this.$store.state.transactions
      return list.sort(this.txSort())
    }
  },
  data() {
    return {
    }
  },
  methods: {
    formatTxInfo(transaction) {
      switch (transaction.type) {
        case config.txTypes.approve: {
          const tokenItem = common.getTokenByAddress(transaction.token)
          if (null == tokenItem) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          return `${this.$t('txType.approve')} ${tokenItem.symbol}`
        }
        case config.txTypes.depositMargin: {
          const poolItem = common.getPoolByAddress(transaction.to)
          if (null == poolItem) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          return `${this.$t('txType.depositMargin')} ${common.weiToUnit(transaction.amount, poolItem.token.decimals)} ${poolItem.token.symbol}`
        }
        case config.txTypes.withdrawMargin: {
          const poolItem = common.getPoolByAddress(transaction.to)
          if (null == poolItem) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          return `${this.$t('txType.withdrawMargin')} ${common.weiToUnit(transaction.amount, poolItem.token.decimals)} ${poolItem.token.symbol}`
        }
        case config.txTypes.create: {
          const poolItem = common.getPoolByAddress(transaction.to)
          if (null == poolItem) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          const tokenItem = poolItem.token
          if (null == tokenItem) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          return `${this.$t('txType.create')} ${common.weiToUnit(transaction.amount, tokenItem.decimals)} ${tokenItem.symbol}`
        }
        case config.txTypes.delivered: {
          return `${this.$t('txType.delivered')}, orderId=${transaction.orderId.toString()}`
        }
        case config.txTypes.denyDelivered: {
          return `${this.$t('txType.denyDelivered')}, orderId=${transaction.orderId.toString()}`
        }
        case config.txTypes.withdrawTimeoutOrder: {
          return `${this.$t('txType.withdrawTimeoutOrder')}, orderId=${transaction.orderId.toString()}`
        }
        case config.txTypes.frozeOrder: {
          return `${this.$t('txType.frozeOrder')}, orderId=${transaction.orderId.toString()}`
        }
        case config.txTypes.confirmReceived: {
          return `${this.$t('txType.confirmReceived')}, orderId=${transaction.orderId.toString()}`
        }
        case config.txTypes.confirmNotDelivered: {
          return `${this.$t('txType.confirmNotDelivered')}, orderId=${transaction.orderId.toString()}`
        }
        case config.txTypes.updatePayerInfo: {
          return this.$t('txType.updatePayerInfo')
        }
        case config.txTypes.updatePayeeInfo: {
          return this.$t('txType.updatePayeeInfo')
        }
        case config.txTypes.updatePayeeLimit: {
          return this.$t('txType.updatePayeeLimit')
        }
        case config.txTypes.donate: {
          const tokenItem = common.getTokenByAddress(transaction.token)
          if (null == tokenItem) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          return `${this.$t('txType.donate')} ${common.weiToUnit(transaction.amount, tokenItem.decimals)} ${tokenItem.symbol}`
        }
        case config.txTypes.burnDtoken: {
          if (null === this.$store.state.dtoken) {
            return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
          }
          const dtokenSymbol = this.$store.state.dtoken.symbol
          const dtokenDecimals = this.$store.state.dtoken.decimals

          return `${this.$t('txType.burnDtoken')} ${common.weiToUnitWithDecimals(transaction.amount, dtokenDecimals)} ${dtokenSymbol}`
        }
        case config.txTypes.createPool: {
          return `${this.$t('txType.createPool')}`
        }
        default:
          return this.isMobile ? this.formatAddress(transaction.hash) : transaction.hash
      }
    },
    txPending(tx) {
      return common.getTxStatus(tx) === 'pending'
    },
    txSort() {
      return (a1, a2) => {
        const p1 = this.txPending(a1)
        const p2 = this.txPending(a2)
        if (p1 === p2) {
          return a2.createTime - a1.createTime
        } else {
          if (p1) {
            return -1
          } else {
            return 1
          }
        }
      }
    }
  }
}
</script>
