<template>
  <div>
    <div class="sec_form_panel">
      <div class="sec_form_item">
        <div class="sec_form_item_title">
          <span>{{$t('account.form.title')}}:</span>
        </div>
        <div class="sec_form_item_content">
          <el-input type="textarea" style="min-width: 280px;" :rows="2" maxlength="32" show-word-limit v-model="accountInfoForm.title"></el-input>
        </div>
      </div>
      <div class="sec_form_item">
        <div class="sec_form_item_title">
          <span>{{$t('account.form.content')}}:</span>
        </div>
        <div class="sec_form_item_content">
          <el-input type="textarea" style="min-width: 280px;" :rows="2" maxlength="32" show-word-limit v-model="accountInfoForm.content"></el-input>
        </div>
      </div>
    </div>
    <div style="width: 100%; margin-top: 10px; text-align: center;">
      <tx-commit-button :tx-loading="txLoading" :tx-form-check="txFormCheckAccount"></tx-commit-button>
    </div>

    <div v-if="accountType === roles.receiver" style="margin-top: 20px;">
      <div class="sec_form_panel">
        <div class="sec_form_item">
          <div class="sec_form_item_title">
            <span>{{$t('account.form.minAmount')}}:</span>
          </div>
          <div class="sec_form_item_content">
            <amount-input v-model="receiverForm.minAmount" :symbol="tokenSymbol" :decimals="tokenDecimals"></amount-input>
          </div>
        </div>
      </div>
      <div style="width: 100%; margin-top: 10px; text-align: center;">
        <tx-commit-button :tx-loading="txLoading" :tx-form-check="txFormCheckLimit"></tx-commit-button>
      </div>
    </div>

    <el-dialog
        :visible.sync="txConfirmDialogVisible"
        custom-class="wallet-dialog"
        :append-to-body="true"
        :title="$t('common.txConfirmDialogTitle')"
        width="90%">
      <tx-confirm ref="txConfirm" v-model="hash" :tx-form="txForm" :call-func="txConfirmCall">
        <div style="width: 100%;" slot="content">
          <div class="sec_form_panel" v-if="isUpdateReceiverLimit">
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('account.form.minAmount')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{receiverForm.minAmount}} {{tokenSymbol}}
              </div>
            </div>
          </div>
          <div class="sec_form_panel" v-else>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('account.form.title')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{accountInfoForm.title}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('account.form.content')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{accountInfoForm.content}}
              </div>
            </div>
          </div>
        </div>
        <div slot="info">
          <template v-if="isUpdateReceiverLimit">
            <span style="font-size: 20px;">{{$t('account.form.minAmount')}} {{receiverForm.minAmount}} {{tokenSymbol}}</span>
          </template>
          <template v-else>
            <span style="font-size: 20px;">{{$t('account.form.title')}} {{accountInfoForm.title}}</span><br>
            <span style="font-size: 20px;">{{$t('account.form.content')}} {{accountInfoForm.content}}</span>
          </template>
        </div>
      </tx-confirm>
    </el-dialog>
  </div>
</template>
<script>
import TxCommitButton from '@/components/common/tx_commit_button'
import AmountInput from '@/components/common/amount_input'
import TxConfirm from '../tx/tx_confirm'
import pool from "@/utils/pool"
import common from "@/utils/common"
import base from "@/utils/base"
import tx_base from "@/utils/tx_base"
export default {
  mixins: [base, tx_base],
  name: 'AccountInfo',
  props: {
    accountType: {
      type: String,
    }
  },
  components: {
    TxConfirm,
    TxCommitButton,
    AmountInput
  },
  computed: {
    isUpdateReceiverLimit() {
      if (null === this.txForm) {
        return false
      }
      return this.txForm.type === common.getTxTypes().updatePayeeLimit
    }
  },
  data() {
    return {
      txConfirmDialogVisible: false,
      accountInfoForm: {
        title: '',
        content: '',
      },
      receiverForm: {
        minAmount: 0
      },
    }
  },
  created() {
    this.getInfo()
    this.getPayeeLimit()
  },
  methods: {
    async getInfo() {
      if (this.poolItem === null || this.account === '') {
        return
      }
      let info = null
      if (this.accountType === this.roles.receiver) {
        info = await pool.payeeInfos(this.account)
      } else {
        info = await pool.payerInfos(this.account)
      }
      this.accountInfoForm.title = common.hexToString(info.title)
      this.accountInfoForm.content = common.hexToString(info.content)
    },
    async getPayeeLimit() {
      if (this.poolItem === null || this.account === '') {
        return
      }
      if (this.accountType === this.roles.receiver) {
        const limit = await pool.payeeLimits(this.account)
        this.receiverForm.minAmount = common.weiToUnit(limit)
      }
    },
    txFormCheckAccount() {
      return {
        success: true,
        func: this.onCommitInfo
      }
    },
    txFormCheckLimit() {
      return {
        success: true,
        func: this.onCommitReceiverLimit
      }
    },
    onCommitInfo() {
      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }
      this.txForm = {
        to: this.poolItem.address,
        title: this.accountInfoForm.title,
        content: this.accountInfoForm.content,
      }

      if (this.accountType === this.roles.receiver) {
        this.txForm.type = common.getTxTypes().updatePayeeInfo
        this.txConfirmCall = () => {
          return pool.updatePayeeInfo(
              common.stringToHex(this.accountInfoForm.title),
              common.stringToHex(this.accountInfoForm.content))
        }
      } else if (this.accountType === this.roles.sender) {
        this.txForm.type = common.getTxTypes().updatePayerInfo
        this.txConfirmCall = () => {
          return pool.updatePayerInfo(
              common.stringToHex(this.accountInfoForm.title),
              common.stringToHex(this.accountInfoForm.content))
        }
      }

      this.txConfirmDialogVisible = true
    },
    onCommitReceiverLimit() {
      if (this.accountType !== this.roles.receiver) {
        return
      }
      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }

      this.txForm = {
        type: common.getTxTypes().updatePayeeLimit,
        to: this.poolItem.address,
        minAmount: common.unitToWei(this.receiverForm.minAmount),
      }
      this.txConfirmCall = () => {
        return pool.updatePayeeLimit(this.txForm.minAmount)
      }
      this.txConfirmDialogVisible = true
    },
  }
}
</script>
