<script>
import base from "@/utils/base"
import db from "@/utils/db";
import common from "@/utils/common";
import SecCard from './sec_card'
import TxList from './tx/tx_list'
import Account from './account/account'
import {on} from "element-ui/src/utils/dom";
const TxCommitButton = () => import('@/components/common/tx_commit_button')
const Network = () => import('@/components/network')
const TokenSelect = () => import('@/components/token_select')

export default {
  mixins: [base],
  name: 'SecHeader',
  props: {
  },
  components: {
    SecCard,
    TxCommitButton,
    Network,
    TokenSelect,
    TxList,
    Account,
  },
  data() {
    return {
      selectToken: '',
      inviteUrlDialogVisible: false,
      showMenusPopover: false,
      txListDialogVisible: false,
      walletDialogVisible: false,
    }
  },
  computed: {
    menus() {
      return [
        {
          text: this.$t('header.account'),
          value: 'account'
        },
        {
          text: this.$t('invite.btnTitle'),
          value: 'invite'
        },
        {
          text: this.$t('footer.home'),
          value: 'home'
        },
        {
          text: this.$t('footer.docs'),
          value: 'docs'
        },
        {
          text: this.$t('footer.developers'),
          value: 'developers'
        },
        {
          text: this.$t('footer.governance'),
          value: 'governance'
        },
        {
          text: 'English/中文',
          value: 'language'
        },
      ]
    },
    inviteUrl() {
      return `https://app.dc2c.finance?inviter=${this.account}`
    },
    pendingTxCount() {
      const txs = this.$store.state.transactions
      let pendingCount = 0
      txs.forEach(item => {
        if (common.getTxStatus(item) === 'pending') {
          pendingCount++
        }
      })
      return pendingCount
    },
  },
  created() {
    on(document, 'click', e => {
      if (this.$refs['header-menus-btn']) {
        if (this.$refs['header-menus-btn'].contains(e.target)) {
          return
        }
      }

      if (this.$refs['header-menus']) {
        if (!this.$refs['header-menus'].contains(e.target)) {
          this.showMenusPopover = false
          return
        }
      }
    });
    this.selectToken = this.tokenAddress
  },
  methods: {
    openWallet() {
      this.walletDialogVisible = true
    },
    addressClickFunc() {
      let msg = this.formatAddress(this.account)
      if (this.pendingTxCount > 0) {
        msg = this.pendingTxCount + ' ' + this.$t('common.pending')
      }
      return {
        success: true,
        msg: msg,
        func: this.openTxListDialog
      }
    },
    openTxListDialog() {
      this.txListDialogVisible = true
    },
    async goClearTx() {
      await db.clearData()
    },
    onMenuSelected(val) {
      if (val.value === 'language') {
        this.onLangClick()
      } else if (val.value === 'invite') {
        this.goInviteUrl()
      } else if (val.value === 'account') {
        this.openWallet()
      } else if (val.value === 'docs') {
        this.goDocs()
      } else if (val.value === 'developers') {
        this.goDevelopers()
      } else if (val.value === 'governance') {
        this.goGovernance()
      } else if (val.value === 'home') {
        this.goDc2cHome()
      }
      if (val.value !== 'language') {
        this.showMenusPopover = false
      }
    },
    goHome() {
      this.$router.push({
        path: '/'
      })
    },
    onLangClick() {
      let locale = this.$i18n.locale
      if (locale === 'zh') {
        locale = 'en'
      } else {
        locale = 'zh'
      }
      this.$i18n.locale = locale
      localStorage.setItem('locale', locale)
    },
    async onSelectTokenChange() {
    },
    goInviteUrl() {
      this.inviteUrlDialogVisible = true
    }
  }
}
</script>
